div#shortcutbar {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 32px;
  width: 100%;
  padding: 0px;
  margin: 0px;
  background-color: #414141;

  button.shortcutbarbutton {
    display: inline-block;
    padding: 0px;
    margin: 0px;
    width: 32px;
    height: 32px;
    border: none;
    background-color: #414141;
    outline: none;

    &:hover {
      background-color: #313131;
      cursor: pointer;
      color: #8c8c8c; }

    &:active {
      outline: none;
      background-color: #212121;
      color: #7d7d7d; }


    i {
      margin: 0px;
      padding: 0px;
      color: #fff;
      font-size: 24px; } } }
